@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
body {
  margin: 0;
  font-family: "Roboto", sans-serif, "Segoe UI", "Roboto", "Oxygen", "Ubuntu",
    "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
  font-size: 14px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@media (max-width: 991.98px){body{font-size:12px}}.container{padding:3rem 0}.container>h2{margin-bottom:4rem;margin-left:2rem}.d-flex{display:flex}.mt-1{margin-top:1rem}h1,h2,h3,h4,h5,h6,p{margin:0;padding:0;color:#444}.cards{display:grid;grid-template-columns:repeat(2, 1fr);justify-items:center;grid-row-gap:3rem}@media (max-width: 771.98px){.cards{grid-template-columns:repeat(1, 1fr)}}.text-color-white{color:#ffffff}

