rem;background-color:#ccf5e7;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;border-top-right-radius:100px;border-bottom-right-radius:100px;font-weight:600}.card-container .card-body .left-container .offer-bar span{color:#6bc7b0}.card-container .card-body .right-container{text-align:end;display:flex;flex-direction:column}.card-container .card-body .right-container .original-fees-container{justify-content:flex-end;align-items:center}.card-container .card-body .right-container .discounted-fees-container{font-size:1.25rem}.card-container .card-body .right-container .discounted-fees-container h1{color:#dd1c26}.card-container .card-body .right-container .discounted-fees-container small{font-size:0.8rem}.card-container .card-body .right-container .amenties-container{display:flex;padding:0.75rem 0 0.75rem;font-weight:600;align-items:center;height:100%;flex-wrap:wrap}.card-container .card-body .right-container .amenties-container p{color:#6bc7b0}.card-container .card-body .right-container .amenties-container p .dot{border-radius:50%;width:5px;height:5px;background-color:#6bc7b0;margin:3px 0.4rem;display:inline-block}

.rating-container{position:absolute;right:1rem;top:3rem;background-color:#f5a626;padding:0.6rem 1rem 1rem;border-radius:4px}.rating-container .value{display:flex;justify-content:center;align-items:center;color:#ffffff}.rating-container .value b{font-size:1.5rem;letter-spacing:0.25rem}.rating-container .text{color:#ffffff}

.ribbon-container{position:absolute;top:-25px;right:0px;overflow:visible;font-size:18px;line-height:18px}.ribbon-container .ribbon{display:block;padding:1rem 3rem;position:relative;background:#68a7ff;overflow:visible;height:18px;margin-left:29px;color:#fff;text-decoration:none}.ribbon-container .ribbon::before{content:"";position:absolute;top:0;left:-28px;border-top:25px solid #68a7ff;border-bottom:25px solid #68a7ff;border-right:29px solid transparent;border-left:29px solid transparent}

.tag{display:inline-block;background-color:#dd1c26;border-left:1px solid #dd1c26;margin-left:19px;position:relative;color:white;padding:2px 8px}.tag::before{content:"";position:absolute;left:-18px;top:0;border-top:10px solid transparent;border-bottom:10px solid transparent;border-right:9px solid #dd1c26;border-left:8px solid transparent}.tag::after{content:"";background-color:white;border-radius:50%;width:5px;height:5px;position:absolute;left:0px;top:8px}

.star{font-size:7px;position:relative;display:inline-block;width:0;height:0;margin-left:0.9em;margin-right:0.9em;margin-bottom:1.2em;border-right:0.3em solid transparent;border-bottom:0.7em solid #a2a2a2;border-left:0.3em solid transparent}.star:before,.star:after{content:"";display:block;width:0;height:0;position:absolute;top:0.6em;left:-1em;border-right:1em solid transparent;border-bottom:0.7em solid #a2a2a2;border-left:1em solid transparent;transform:rotate(-35deg)}.star:after{transform:rotate(35deg)}.star.dark{border-bottom-color:#444444}.star.dark::before,.star.dark::after{border-bottom-color:#444444}

